import React,{useState,useEffect,useRef} from 'react'
import ReactDOM from 'react-dom'
import NumberFormat from 'react-number-format'
import { inject, observer } from 'mobx-react';
import styled from 'styled-components';
import { toPlainString,countDecimal } from './utils';
import { bg } from '../lib/web3js';
const Wrapper = styled(NumberFormat)`
  width : ${props => props.width}px;
`


function DeriNumberFormat(props){
  const [renderablity, setRenderablity] = useState(<span className='loading-line'></span>);
  const [firstTime, setFirstTime] = useState(true)
  const ref = useRef()
  const isValidate = () => {
    const {allowZero} = props 
    return (typeof props.value !== 'object' && props.value !== undefined && props.value !== '' && !isNaN(props.value) && props.value !== 'NaN') 
          || ((isNaN(props.value) && Math.abs(props.value)) === 0 && allowZero === true )
  }

  useEffect(() => {
    const {allowZero,wallet,defaultValue = '--',...others} = props 
    if(isValidate()) {
      if(others.value && /\d+\.0*[1-9]+/.test(others.value)  && (+bg(others.value).toFixed((+others.decimalScale || 2))) === 0){
        others.decimalScale = countDecimal(others.value) + 1
      }
      others.value = toPlainString(others.value)
      setRenderablity(<Wrapper {...others}  displayType = 'text'  fixedDecimalScale={true} thousandSeparator={true}/>)
      setFirstTime(false)
    } else if(firstTime){
      setRenderablity(<span className='loading-line' style={{width:props.width,height : props.height}}></span>)
    } 
    const timeout = window.setTimeout(() => {
      // && wallet && (!wallet.isConnected() || !wallet.supportChain)
      if(!isValidate()){
        setRenderablity(defaultValue)
      }
    },30000)
    return () => {
      clearTimeout(timeout)
    };
  }, [props.value,props.decimalScale,props.suffix]);

  return renderablity;
}
export default DeriNumberFormat
// export default inject('wallet')(observer(DeriNumberFormat))